// ###  Create admin user at first start  ###


if (Meteor.users.find().count() === 0) {
	var adminPassword = "admin";

	var users = [
		{username:"admin",roles:['admin']},
	];

	_.each(users, function (user) {
		var id;
		id = Accounts.createUser({
			username: user.username,
			email: "Admin",
			password: adminPassword,
			profile:{name:"Admin"}
		});

		if (user.roles.length > 0) {
			Roles.addUsersToRoles(id, user.roles);
		}
	});
}


if (Apps.find().count() === 0) {

	var defaultApps = [
		{name:"Beekee Live", actual_version:"1.3.3",last_version:"1.3.3", url:"https://live.beekee.ch", icon:"beekee-live.png", description:"Beekee Live promote real-time interaction by allowing learners to express themselves asking questions, posting photos or sharing files.", installed:true, version: "0.1", hidden:false},
		{name:"Beekee Wheel", actual_version:"0.7",last_version:"0.7", url:"https://wheel.beekee.ch", icon:"beekee-wheel.png", description:"A simple random picker wheel.", installed:true, version: "0.1", hidden:false},
		{name:"Moodle", actual_version:"xx",last_version:"xx", url:"https://tecfalms.unige.ch/moodle/", icon:"moodle.png", description:"Moodle is a free, online Learning Management system enabling educators to create their own private website filled with dynamic courses that extend learning, any time, anywhere.", installed:true, version: "0.1", hidden:false},
	];

	_.each(defaultApps, function (defaultApps) {
		Apps.insert(defaultApps);
	});
}